%transition {
  transition: all 0.35s ease-in-out;
}

%font-prim {
  font-family: Inter, sans-serif;
}

%font-add {
  font-family: Merriweather, sans-serif;
}
%custom-scroll {
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 1px grey;
  }
  &::-webkit-scrollbar-thumb {
    background: rgb(177, 177, 238);
    border-radius: 10px;
  }
}
